import React, {Component} from 'react';
import navStyles from './nav.module.css';
import {Link} from 'gatsby';

class Nav extends Component{

    componentDidMount(){
        window.addEventListener('scroll', this.handleScroll)
        this.handleScroll();
      }      
    
      componentWillUnmount(){
        window.removeEventListener('scroll', this.handleScroll)
      }
       
      //to toggle nevbar events on scroll height
      handleScroll = (event) =>{
        const nav = document.getElementById('nav').classList; 
        window.scrollY > 97? nav.add('fixed', 'bg-black-20', 'z-max') : nav.remove('fixed', 'bg-black-20', 'z-max');
      } 

    render(){
        return(
            <nav id="nav" className={`pt2 absolute dt w-100 center ${navStyles.pos}`}>
                <div className="dtc w5 v-mid ">
                    <Link to="/" className="link no-underline black f3 fw4 grow pointer dib-ns ph3 ml3 mb3">OurLabs</Link>
                </div>
                <div className="dtc tr pt2 ">
                    <Link to="/about" className="link f5 fw5 grow pointer  dib-ns pr4 mb3">About</Link>
                    
                    {/* <Link className="f5 fw5 grow pointer dn dib-ns pr4 mb3"></Link> */}
                </div>
          </nav>
    
        );
    }
}    

export default Nav;